        methods: {
            onChange(index) {
                this.activeKeyLeft = index
                this.getDetailFindById().then(res => {
                    this.photo = res[this.activeKeyLeft].photo
                    this.name = res[this.activeKeyLeft].name
                    this.description = res[this.activeKeyLeft].description
                })
            },
            async getDetailFindById() {
                let data = {
                    page: 1,
                    pageSize: 10,
                    productCategoryId: this.productCategoryId
                }
                let res = await get('/product/pageQuery', data)
                this.orderDetailList = res.data.data.list
                return this.orderDetailList
            },
            // 跳转到对应的链接
            submitOrder() {
                // 使用编程式导航进行跳转
                this.$router.push({
                    // 路径
                    path: '/manager/product',
                    // 参数的传递
                    query: {
                        categortId: this.productCategoryId,
                        activeKey: this.activeKey
                    }
                })
            }
        },
    }
</script>
